table.board {
  height: calc(100% - 50px); /* 50px goes to header */
  width: calc(100% + 1px);
  background: black;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
  border-spacing: 0;
  border-collapse: collapse;
}
.board td {
  border: 1px solid #8e8e8e;
}
.board.borderless td {
  border: none;
}
.board td > div {
  width: 96%;
  height: 96%;
  margin: auto;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  position: relative;
}
.board td > div > span {
  width: 100%;
  height: 100%;
  display: block;
}
.board td.snake > div {
  background: #bdbebd;
}
.board td.snake.head > div {
  background-image: url("../../assets/img/snake_head.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
}
.board td.snake.head.left > div {
  transform: rotate(180deg);
}
.board td.snake.head.up > div {
  transform: rotate(270deg);
}
.board td.snake.head.down > div {
  transform: rotate(90deg);
}
.board td.snake > div > span {
  width: 60%;
  height: 60%;
  margin: auto;
  border-radius: 50%;
}
.board td.food > div > span {
  -webkit-transition: all 1s ease 0s;
  -moz-transition: all 1s ease 0s;
  -o-transition: all 1s ease 0s;
  transition: all 1s ease 0s;
}
.board .red span {
  background-color: #ff3f94;
}
.board .green span {
  background-color: #22a900;
}
.board .blue span {
  background-color: #00c8ff;
}
.board .gold span {
  background-color: #ffd759;
}
.board .orange span {
  background-color: #ff7c00;
}
.board .black span {
  background-color: #8e8e8e;
}

/*THEME - deSnaker - START*/
.deSnaker .board td.snake.head > div {
  background: #00c8ff;
}
.deSnaker .board td.snake.head > div > span {
  background-image: url("data:image/svg+xml;base64,");
  background-size: contain;
  border-radius: 0;
  background-repeat: no-repeat;
}
.deSnaker .board td > div > span {
  background-color: transparent;
  background-repeat: no-repeat;
}
.deSnaker .board td > div > span {
  width: 50%;
  height: 50%;
  margin: auto;
  transition: none;
}
.deSnaker .board td.food > div {
  border-radius: 0;
  background-color: white;
}
