}
.deSnaker .board .green span {
  background-image: url("data:image/svg+xml;base64,");
}
.deSnaker .board .red span {
  background-image: url("data:image/svg+xml;base64,");
}
.deSnaker .board .gold span {
  background-image: url("data:image/svg+xml;base64,");
}
.deSnaker .board .black span {
  background-image: url("data:image/svg+xml;base64,");
}
.deSnaker .board td.snake > div > span {
  border-radius: 0;
}
.deSnaker .board td.snake > div > span {
